import React, { useEffect, useState } from 'react';
import logo from '../images/watsbulkAsset 7.svg';
import Button from 'react-bootstrap/Button';
import { navigate } from 'gatsby'
import { verify } from '../api_handlers/account_api';

const headerStyles = {
  height: 65,
    color: 'rgb(238, 238, 238)',
    display: 'grid',
    gridTemplateColumns: '1fr 6fr 1fr',
    padding: '0.6rem',
    placeItems:'center',
    background: 'rgba(0,103,86,255)',
    boxShadow: '0 8px 32px 0 rgba(0,103,86,255)',
    backdropFilter: 'blur( 16.5px )',
  fontFamily:'sans-serif',
  fontWeight:'700'
}

const logoContainer = {
	display:'flex',
	alignItems:"flex-end",
}

const logo_ = {
	width:'2.5rem',
	margin:'0 10px'
}
export default function Navbar(){
	let [action , setAction]= useState({text:'Login',action:()=>{
    localStorage.removeItem('CSRF_token');
    navigate('/login')}
  })
useEffect(()=>{
  let token = localStorage.getItem('CSRF_token')
  if(token === 'undefined' || token === 'null' || !token){
    if(location.href.includes('/login')){
      console.log(location.href)
      setAction({text : 'Signup' , action : ()=>navigate('/signup')})
      return
    }
    else if(location.href.includes('signup')){
      setAction({text:'Login',action:()=>navigate('/login')})
      return
    }
    else if(!location.href.includes('login') && !location.href.includes('signup')){

      navigate('/login')
    }
  }
  if(token){
    verify()
    .then(isMatch=>{
      if(!isMatch){
        if(location.href.includes('signup')){
          setAction({
            text:'Login',action:()=>{
              localStorage.removeItem('CSRF_token');
    navigate('/login')}
            })
            return
          }
          if(location.href.includes('login')){
            setAction({
              text:'Sign up',action:()=>{
                localStorage.removeItem('CSRF_token');
      navigate('/signup')}
              })
              return
            }
          }    
      if(!isMatch.success){
        if(!location.href.includes('/login')){
        navigate('/login')
      } return
    }
      if(isMatch.success){
        let store_ = JSON.parse(localStorage['store_']);
        store_.email = isMatch.email;
        store_.number = isMatch.number;
        localStorage.setItem('store_',JSON.stringify(store_));
        setAction(i=>{
          return {
            text : 'Logout',
            action:()=>{
            localStorage.removeItem('CSRF_token');
            navigate('/login')}
          } });
        if(location.href.includes('login') || location.href.includes('signup')){
          navigate('/products');
        }
      }
    
    })
    .catch(e=>{
      console.log(e.data)
    })
}


},[])
  
	return(
<header >
    <nav style={headerStyles}>
        <div style={logoContainer}>
        <img src={logo} style={logo_}/>
        <span>watstore</span>
         </div>
         <div></div>
         <Button variant="outline-light" onClick={action.action}>{action.text}</Button>
    </nav>
    </header>
)}