import React , { useState , useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import Container from 'react-bootstrap/esm/Container';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';

import Navbar from '../components/Navbar';
import Layout from '../components/layout';
import { get_seller_details } from '../api_handlers/dashboard_api';
import { get_products } from '../api_handlers/products_api';
import product_icon from '../images/product_icon.png';
import shop_icon from '../images/shop_icon.png';
import order_icon from '../images/order_icon.png';
import { get_orders } from '../api_handlers/order_api';
import { Sidebar } from '../components/Sidebar';
import {
	AreaChart,
	Area,
	XAxis,
	YAxis,
	CartesianGrid,
	Tooltip,
	ResponsiveContainer
  } from "recharts";
import Piechart from '../components/Piechart';
import Barcharts from '../components/Barcharts';
  

const data = [
  { name: 'Group A', value: 20 },
  { name: 'Empty' , value : 100}

];  
const bar_data = [
	{
	  name: 'Orders Received',
	  uv: 4000,
	  pv: 2400,
	  amt: 2400,
	},
	{
	  name: 'Orders Sent',
	  uv: 3000,
	  pv: 1398,
	  amt: 2210,
	},
	{
	  name: 'Orders Completed',
	  uv: 2000,
	  pv: 9800,
	  amt: 2290,
	},

  ];
  

const centerSingleElement = {
	display:'flex',
	justifyContent:'center',
	alignItems:'center'
}

const wrapper = {
display: 'flex',
height: 'calc(100vh)',
background:'rgb(211 209 203)'
}

const tiles = {
	display:'flex',
	gap:7,
	flexFlow:'column',
	alignItems:'center',
	padding:'2rem 0',
	borderRadius:10,
	background: 'rgb(219 239 205)'
}
const iconWrap = {
	display: 'grid',
    width: 60,
    background: 'rgb(243 255 186)',
    height: 60,
    placeItems: 'center',
    borderRadius: '50%'
}
const icon =  {
	 width:24
	}
const overall_wrap = {
	display:'flex',
	flexFlow:'column',
	padding:'1rem 0',
	borderRadius:10,
	background:'#333',
	marginTop:'3rem'
}

const overall_tiles = {
	display:'flex',
	width:'5rem',
	borderRadius:10
}

const overall_flex_wrap = {
	display:'flex',
	flexFlow:'column',
}

const overall_data = {
	display:'flex',
	flexFlow:'column',
	color:'#fff'
}
const overall_icons = {width:36,height:36,margin:'auto 1rem', filter:'invert(1)'}
export default function Dashboard() {
	const [seller , setSeller] = useState();
	const [orders , setOrders] = useState([]);
	let store_ = JSON.parse(localStorage.getItem('store_'))
	useEffect(()=>{
		get_seller_details()
		.then(data=>{
			setSeller(data)
		console.log(data)});

		get_orders()
		.then(data=>setOrders(data))
	},[]);
	return(
<Layout>
		

		<div id="wrapper" style={wrapper}>
		<Sidebar />
		<Container fluid={'lg'} style={{padding:'35px 0px 0 12.5rem',minWidth:'70vw'}}>
		<Row>
		<Col>
		<h2>Welcome to</h2>
		<h4>{store_.shop_name}'s Panel</h4>
		</Col>
		<Col></Col>
		</Row>
		<Row>
		<Col>
		<div style={tiles}>
		<div style={iconWrap}>
		<img style={{...icon , filter:'invert(1)'}} src={product_icon}/>
		</div>
		<h6 style={{fontSize:14, color:'#222'}}>Products</h6>
		<h2>{345}</h2>
		<h6 style={{fontSize:14 , color:'#222'}}>Uploaded</h6>
		</div>
		</Col>
		<Col>
		<div style={{...tiles , background:'rgb(239 205 205)'}}>
		<div style={iconWrap}>
		<img style={{...icon }} src={order_icon}/>
		</div>
		<h6 style={{fontSize:14, color:'#222'}}>Orders</h6>
		<h2>{0}</h2>
		<h6 style={{fontSize:14 , color:'#222'}}>Active</h6>
		</div>
		</Col>
		<Col>
		<div style={{...tiles,background:'rgb(205 239 226)'}}>
		<div style={iconWrap}>
		<img style={{...icon }} src={shop_icon}/>
		</div>
		<h6 style={{fontSize:14, color:'#222'}}>Sales</h6>
		<h2>{345}</h2>
		<h6 style={{fontSize:14, fontWeight:500 , color:'#222'}}>This week</h6>
		</div>
		</Col>
		</Row>
		<Row>
		
		<div style={{borderRadius:10,background:'rgb(28 179 98)' , height:'20rem',margin:'2rem 0 0 1rem' , width:'70vw' , display:'flex' , flexFlow:'column'}}>
		<h3 style={{margin:'0 0 2rem 0',padding:'1rem 0',color:'#222'}}>This Week's Sales</h3>
		<div style={{display:'flex',marginTop:'-10rem' , justifyContent:'space-around'}}>
		<Piechart data={data}/>
		<Piechart data={data}/>
		<Piechart data={data}/>
		</div>
		</div>
		
		</Row>
		</Container>
		<Container style={{width:'20vw' , color:'#fff'}}>
		<Row>
		<Col>
		<div style={overall_wrap}>
		<h2 style={{color:'#fff' , margin:'1rem'}}>Overall Performance</h2>
		<div style={overall_flex_wrap}>
		<div style={overall_tiles}>
		<img src={order_icon} style={overall_icons}/>
		<div style={overall_data}>
		<h6>Total Sales</h6>
		<h1>{77}</h1>
		</div>
		</div>
		<div style={overall_tiles}>
		<img src={order_icon} style={overall_icons}/>
		<div style={overall_data}>
		<h6>Revenue Generated</h6>
		<h1>{'77k'}</h1>
		</div>
		</div>
		<div style={overall_tiles}>
		<img src={order_icon} style={overall_icons}/>
		<div style={overall_data}>
		<h6>Views</h6>
		<h1>{'550k'}</h1>
		</div>
		<Barcharts data={bar_data}/>
		</div>
		
		</div>
		</div>
		</Col>
		</Row>
		</Container>
		</div>
</Layout>
)
	}