import React, { PureComponent } from 'react';
import { PieChart, Pie, Sector, Cell, ResponsiveContainer } from 'recharts';

const COLORS = ['#ff8042', '#000', '#FFBB28', '#FF8042'];

export default class Piechart extends PureComponent {
 

  render() {
    let {data} = this.props;
    return (
      <PieChart width={240} height={440} onMouseEnter={this.onPieEnter}>
        <Pie
          data={data}
          cx={120}
          cy={200}
          innerRadius={50}
          outerRadius={70}
          fill="#8884d8"
          paddingAngle={6}
          dataKey="value"
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>

      </PieChart>
    );
  }
}
